import styled from '@emotion/styled';
import { useModal } from '../Hooks/useModal'
import { ModalTypes } from '../Types';

export const Button: ModalTypes['Parts']['Button'] = (props) => {
  const { type, afterClick, children = "Button" } = props

  const { closeModal } = useModal()

  const handleClick = async () => {
    if (type === 'Close') {
      closeModal();
    }
    if (afterClick) {
      const fn = typeof afterClick === 'function' ? afterClick : afterClick[type];
      if (fn) {
        const result = fn();
        if (result instanceof Promise) {
          await result;  // 비동기 함수일 경우
        }
        // else {} 동기 함수일 경우 이미 위에서 실행됨
      }
    }
  };
  
  


  return (
    <ButtonWrapper onClick={handleClick}>
      {children}
    </ButtonWrapper>
  )
}

const ButtonWrapper = styled.button<ModalTypes['Props']['Button']>`
  cursor: pointer;

  &:hover {
    color: #007BFF;
  }
`